@import '../../style/themes/index';
@import '../../style/mixins/index';

@select-transfer-prefix-cls: ~'@{sens-prefix}-select-transfer';
@select-panel-prefix-cls: ~'@{sens-prefix}-select-panel';
@transfer-border-color: @outline-color;

.reset() {
  margin: 0;
  padding: 0;
}

.@{select-transfer-prefix-cls} {
  .reset-component();
  min-width: 528px;

  .@{select-panel-prefix-cls} {
    &-statistics {
      margin-top: 0;
    }

    &-content {
      position: relative;
      margin-bottom: 0;
    }

    &-no-result {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &-footer {
      height: 46px;
    }
  }

  .@{select-panel-prefix-cls}-option-active {
    font-weight: normal;
    background-color: #fff;

    &:hover {
      background-color: @background-01-transparent;
    }
  }

  &-left,
  &-right {
    flex: 1;
    height: 100%;
    border: 1px solid @transfer-border-color;
    border-radius: 4px;
  }

  &-right {
    position: relative;
    background-color: #fff;

    .@{select-transfer-prefix-cls}-selected {
      height: calc(100% - 46px);

      &-noData {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
      }

      &-container {
        .reset();
        padding-top: 6px;
      }

      &-item {
        padding: 6px 12px;

        &:hover {
          background-color: @background-transparent-grey-hover;
        }

        &-title {
          display: flex;
          align-items: center;
          justify-content: space-between;
          cursor: default;
        }

        &-label {
          .reset();
          width: calc(100% - 24px);
          color: @text-color-transparent;
          font-size: 14px;
        }

        &-detail {
          margin-top: 8px;
          color: @text-sub-color-transparent;
          font-size: 12px;
          cursor: default;
        }

        &-disabled {
          color: @text-color-transparent-disable;

          &:hover {
            color: @text-color-transparent-disable-hover;
            background-color: @background-transparent-grey-hover;
            cursor: not-allowed;
          }

          .@{select-transfer-prefix-cls}-selected-item-label,
          .@{select-transfer-prefix-cls}-selected-item-detail {
            color: @text-color-transparent-disable;
          }
        }
      }

      &-footer {
        position: absolute;
        bottom: 0;
        display: flex;
        align-items: center;
        width: 100%;
        height: 46px;
        padding: 12px;
        background-color: #fff;
        border-top: 1px solid @transfer-border-color;
        border-bottom-right-radius: 6px;
        border-bottom-left-radius: 6px;
      }

      &-count {
        margin-left: 4px;
        color: @text-sub-color-transparent;

        > span {
          font-size: 12px;
        }
      }
    }
  }

  &-ellipsis.@{sens-prefix}-typography {
    color: inherit;
  }
}

