@import '../../style/themes/index';
@import '../../style/mixins/index';

@typography-cls: ~'@{sens-prefix}-anchor-typography';

@anchor-border-width: 2px;
@anchor-width: 180px;

.@{sens-prefix}-anchor {
  .reset-component();

  position: relative;
  padding-right: 8px;
  padding-left: calc(@anchor-border-width + 6px);

  .@{typography-cls} {
    color: @text-sub-color-transparent;
  }

  &-wrapper {
    width: @anchor-width;
    padding: 16px 0 24px;
    overflow: auto;
    background-color: @anchor-bg;
    border-radius: 3px;
  }

  &-ink {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;

    &::before {
      position: relative;
      display: block;
      width: @anchor-border-width;
      height: 100%;
      margin: 0 auto;
      background-color: @divideline-color-transparent-light;
      content: ' ';
    }

    &-ball {
      position: absolute;
      left: 50%;
      display: none;
      width: 2px;
      height: 14px;
      background-color: @component-background;
      border: 1px solid @component-primary;
      transform: translateX(-50%);
      transition: top 0.3s ease-in-out;

      &.visible {
        display: inline-block;
      }
    }
  }

  &-fixed &-ink &-ink-ball {
    display: none;
  }

  &-link {
    &:last-child {
      margin-bottom: 0;
    }

    &-title {
      position: relative;
      display: block;
      margin-bottom: 4px;
      padding: 7px 0 7px 12px;
      color: @text-color;
      line-height: @line-height;
      border-radius: 4px;
      transition: all 0.3s;

      &:hover {
        background: @background-transparent-grey-hover;

        .@{typography-cls} {
          color: @component-hover;
        }
      }

      &:active {
        background: @background-01-transparent;

        .@{typography-cls} {
          color: @component-active;
        }
      }
    }

    &-active > &-title .@{typography-cls} {
      color: @component-primary;
      font-weight: 500;
    }
  }

  &-link &-link {
    margin-bottom: 0;
  }

  // collapsible anchor
  &-collapsible {
    width: @anchor-width;

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 40px;
      margin-bottom: 8px;
      margin-left: calc(@anchor-width - 40px);
      padding: 12px;
      color: @icon-color-transparent;
      background: @white;
      border-radius: 24px;
      box-shadow: @shadowbox-3-down;
      cursor: pointer;

      > span {
        font-size: 16px;
      }

      &:hover {
        color: @component-primary;
      }
    }

    .@{sens-prefix}-anchor-wrapper {
      box-shadow: @shadowbox-3-down;
    }
  }

  &-collapsible &-ink {
    &::before {
      background-color: transparent;
    }
  }

  // collapsed
  &-collapsed {
    display: none;
  }

  // horizontal
  &-horizontal {
    .@{sens-prefix}-tabs {
      color: @text-sub-color-transparent;

      .@{sens-prefix}-tabs-nav {
        margin-bottom: 0;

        .@{sens-prefix}-tabs-tab {
          max-width: 112px;
          padding: 0;

          &:hover .@{typography-cls} {
            color: @component-hover;
          }

          &:active .@{typography-cls} {
            color: @component-active;
          }

          & + .@{sens-prefix}-tabs-tab {
            margin-left: 24px;
          }
        }

        .@{sens-prefix}-tabs-tab-active {
          &:hover .@{typography-cls} {
            color: @component-hover;
          }

          .@{typography-cls} {
            color: @component-primary;
          }
        }

        .@{sens-prefix}-tabs-nav-more {
          padding: 4px 16px;
        }

        .@{sens-prefix}-tabs-dropdown-visible {
          .@{sens-prefix}-anchor-more-icon span {
            color: @component-active;
          }
        }
      }
    }
  }

  // horizontal anchor more icon
  &-more-icon {
    display: flex;
    align-items: center;
    cursor: pointer;

    span {
      &:first-child {
        margin-right: 4px;
        white-space: nowrap;
      }

      &:last-child {
        color: @icon-color-transparent;
        font-size: 16px;
      }
    }

    &:hover span {
      color: @component-hover;
    }

    &:active span {
      color: @component-primary;
    }
  }
}

