@import '../../style/themes/index';
@import '../../style/mixins/index';

@select-panel-prefix-cls: ~'@{sens-prefix}-select-panel';
@icon-content-width: @font-size + @control-icon-margin;

.@{select-panel-prefix-cls} {
  .reset-component();
  height: 100%;
  margin-top: @select-panel-vertical-padding;
  background-color: #fff;
  border-radius: @border-radius-basic;

  // ========================= Search =========================
  &-header-search {
    display: flex;
    align-items: center;
    border-bottom: 1px solid @outline-color-transparent;
    transition: all 0.3s;

    &:hover {
      border-color: @component-primary;
    }
  }

  &-search-focused {
    border-color: @component-active;

    &:hover {
      border-color: @component-active;
    }
  }

  &-back-icon {
    display: flex;
    align-items: center;
    padding-left: @select-panel-icon-padding;
    color: @icon-color-transparent;
    cursor: pointer;

    &:hover {
      color: @component-primary;
    }
  }

  &-back-line {
    width: 1px;
    height: 16px;
    padding-left: @select-panel-icon-padding;
    border-right: 1px solid @divideline-color-transparent-dack;
  }

  &-search-input.@{sens-prefix}-input-search {
    border-bottom: 0;
    border-radius: 4px;
  }

  &-search-divider {
    top: 0;
    height: 16px;
    margin-right: 11px;
    border-color: @divideline-color-transparent-dack;
  }

  // ========================= Statistics =========================
  &-statistics {
    padding: @select-panel-vertical-padding @control-padding-horizontal;
    color: @text-sub-color-transparent;
    font-size: @select-panel-des-font-size;
    line-height: @select-panel-des-height;
  }

  // ========================= Content =========================
  &-content {
    margin-bottom: @select-panel-footer-padding;

    .@{sens-prefix}-tree {
      padding-left: 12px;
    }
  }

  &-width-search {
    margin-top: 0;
  }

  // ========================= Options =========================
  &-option {
    min-height: @select-panel-option-height;
    padding: @select-panel-vertical-padding @control-padding-horizontal;
    color: @font-color;
    cursor: pointer;
    transition: background 0.3s ease;

    &:hover {
      background-color: @background-01-transparent;
    }

    // ====== active ======
    &-active {
      font-weight: 600;
      background-color: @component-active-background;

      &:hover {
        background-color: @select-panel-option-active-hover-bg;
      }
    }

    // ====== disabled =====
    &-disabled {
      color: @text-color-transparent-disable;
      background-color: transparent;
      cursor: not-allowed;

      .@{select-panel-prefix-cls}-option-detail {
        color: @text-color-transparent-disable;
      }

      &:hover {
        color: @text-color-transparent-disable-hover;
        background-color: @background-transparent-grey-hover;

        .@{select-panel-prefix-cls}-option-detail {
          color: @text-color-transparent-disable-hover;
        }
      }
    }

    &-content {
      position: relative;
      display: flex;
      align-items: flex-start;
    }

    &-left {
      margin-right: @control-icon-margin;
    }

    &-right {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      display: flex;
      align-items: center;
    }

    &-suffix {
      width: @select-panel-one-icon-width;
    }

    &-text {
      width: 100%;
    }

    &-one-icon {
      padding-right: @select-panel-two-icon-width;
    }

    &-two-icon {
      padding-right: @select-panel-one-icon-width;
    }

    &-suffix-icon {
      margin: 0 @control-icon-margin 0 @horizontal-margin-sm;
    }

    &-ellipsis.@{sens-prefix}-typography {
      color: inherit;
    }

    &-detail {
      margin-top: @select-option-detail-margin-top;
      color: @text-sub-color-transparent;
      font-weight: normal;
      font-size: @select-option-detail-font-size;
      line-height: @select-option-detail-line-height;
    }

    &-group {
      height: @select-panel-group-hight;
      padding: @select-panel-vertical-padding @control-padding-horizontal;
      color: @text-sub-color-transparent;
      background-color: @background-transparent-grey;
    }

    &-group-line {
      height: 1px;
      background-color: @divideline-color-transparent-light;
    }
  }

  // ========================= Footer =========================
  &-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: @select-panel-footer-min-width;
    padding: @vertical-margin-xss @control-padding-horizontal @vertical-margin-xss - 1;
    border-top: 1px solid @divideline-color-transparent-light;
  }

  &-operate-right {
    display: flex;
  }

  // 
  &-word-important {
    color: @component-primary;
  }

  &-nodata {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  &-no-result {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

